@import "../libs/include-media/dist/include-media";
@import "utils/variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

@import "special-reset";
/*=========== SPECIAL =========== */
.hide-page {
	display: none !important; }

body.vspec {
	font-size: $font-size;
	line-height: $line-height;
	//font-family: Tahoma, Geneva, sans-serif
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	//font-family: "Times New Roman", Serif
	color: $spec-text-color;
	background-color: $spec-bg-color; }

body.vspec.fontsize-small {
	font-size: $font-size--sm; }

body.vspec.fontsize-large {
	font-size: $font-size--lg; }

body.vspec.spacing-medium {
	letter-spacing: $letter-spacing--medium; }

body.vspec.spacing-large {
	letter-spacing: $letter-spacing--large; }

body.vspec.schemecolor-white {
	color: $spec-text-color--scheme-white;
	background-color: $spec-bg-color--scheme-white; }

body.vspec.schemecolor-blue {
	color: $spec-text-color--scheme-blue;
	background-color: $spec-bg-color--scheme-blue; }

body.vspec::before {
	content: none; }

.vspec .wrapper,
.vspec .footer {
	padding-left: 70px;
	padding-right: 70px;
	padding-top: 0; }

.inner-page .main > .max-wrap:before {
	display: none; }

@media (max-width: 1439px) {
	.vspec .wrapper,
	.vspec .footer {
		padding-right: 0; } }

@include media('<mob-md') {
	.vspec .wrapper,
	.vspec .footer {
		padding-left: 0; }
	.vspec .wrapper {
		padding-top: 56px; } }

.vspec {
	//.max-wrap
	//argin: 0

	.content {
		padding-top: 30px;
		padding-bottom: 30px; }

	.article-press {
		padding-top: 30px;
		color: inherit; }

	.press-archive + .press-adt .article-press {
		padding-top: 0; }

	.press {
		margin-top: 0; }

	.full-width {
		margin: 0; }

	.spacer {
		display: none; }

	.footer {
		margin-top: 0 !important; }

	//.header-options__aside
	//idth: 60px
	//argin-left: -60px

	//.phs__container
 }	//argin-right: 60px

.vspec path,
.vspec polygon {
	fill: currentColor !important; }

/* font */
.vspec .btn-default,
.vspec .btn-default-alt {
	font-family: inherit; }

.vspec {
	h1, h2, h3, h4, h5, h6 {
		color: inherit; } }

.vspec h1 {
	font-size: $title-font-size;
	line-height: $title-line-height;
	letter-spacing: inherit;
	padding: 0;
	margin-bottom: $bottom-space-item; }

.vspec h2 {
	font-size: $sub-title-font-size;
	line-height: $sub-title-line-height;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h3 {
	font-size: $item-font-size;
	line-height: $item-line-height;
	font-weight: 700;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h4 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 700;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h5 {
	font-size: inherit;
	line-height: inherit;
	text-transform: uppercase;
	font-weight: normal;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec h6 {
	font-size: inherit;
	line-height: inherit;
	font-weight: 500;
	padding: 0;
	margin: 0 0 $bottom-space; }

.vspec p {
	letter-spacing: inherit;
	margin-bottom: $bottom-space;
	padding: 0; }

.vspec .to-print {
	display: none; }

.vspec {
	.img-group {
		display: none; }

	&.imageson .img-group {
		display: block; } }

.vspec {
	img {
		display: none; }

	&.imageson {
		img {
			display: block; } } }

.vspec {
	table {
		td, th {
			color: currentColor;
			border-color: currentColor;
			font-size: inherit;
			line-height: inherit;

			span, p, ul {
				background: none !important;
				color: inherit !important; } }

		caption {
			font-size: $font-size--small;
			line-height: $line-height--small;
			color: inherit;
			font-style: italic; } } }

.vspec hr {
	border-bottom-color: currentColor; }

.vspec .mark {
	color: $spec-bg-color;
	background-color: currentColor; }

.vspec a,
.vspec .dark-text,
.vspec .dark-text-light,
.vspec .text-color-alt {
	color: currentColor;
	-webkit-transition: none;
	transition: none; }

.user-content ul > li {
	&::before {
		.vspec & {
			background-color: currentColor;
			top: 1.9em; }

		.vspec.fontsize-large & {
			top: 1.5em; } }

	&:first-child::before {
		.vspec & {
			top: 0.8em; } } }

.user-content ul ul li {
	&::before {
		.vspec & {
			top: 2em; }

		.vspec.fontsize-large & {
			top: 1.5em; } } }

.vspec .user-content ol > li::before {
	color: currentColor; }

.vspec {
	.user-content {
		.tbl {
			td,
			th,
			th,
			thead td,
			tfoot td {
				color: currentColor;
				background-color: transparent;
				border: 1px solid currentColor;
				font-size: inherit;
				line-height: inherit; }

			tbody tr:nth-child(even) {
				td {
					background-color: transparent; } } } } }

.vspec .user-content blockquote {
	border: 2px solid currentColor;
	background-color: transparent; }

.vspec .user-content blockquote q,
.vspec .user-content blockquote cite {
	font-size: inherit;
	line-height: inherit;
	font-family: inherit;
	color: currentColor; }

.vspec .user-content blockquote q::before,
.vspec .user-content blockquote q::after {
	background-image: none; }

.vspec .user-content .pref {
	font-size: inherit;
	line-height: inherit;
	font-family: inherit;
	color: currentColor; }

.vspec .input-wrap {
	font-size: $font-size;
	line-height: $line-height;
	letter-spacing: $letter-spacing; }

.vspec {
	.breadcrumbs {
		letter-spacing: inherit;
		font-size: $font-size--small;
		line-height: $line-height--small;
		color: currentColor;
		padding: 0;
		margin-bottom: $bottom-space-item; }

	.breadcrumbs, .user-content .breadcrumbs {
		li {
			float: none;
			display: inline;
			padding: 0;
			margin: 0 0.3em 0 0;

			&:before {
				position: relative;
				left: auto;
				top: auto;
				background: none;
				margin-right: 0.5em; } } }

	&.fontsize-large {
		.breadcrumbs, .user-content .breadcrumbs {
			li {
				&:before {
					top: auto; } } } } }

.vspec {
	.file {
		display: block;
		color: inherit;

		.file__info {
			border-color: currentColor;
			letter-spacing: normal;
			text-align: left;

			& > div > b {
				color: inherit; } }

		&:hover {
			.file__info {
				border-color: $spec-text-color;

				&::before {
					background-color: $spec-text-color; }

				& > div,
				& > div > b {
					color: $spec-bg-color; } } } }

	.file__icon {
		display: none; }

	&.imageson {
		.file__icon {
			display: block; } }

	&.schemecolor-white {
		.file {
			&:hover {
				.file__info {
					border-color: $spec-text-color--scheme-white;

					&::before {
						background-color: $spec-text-color--scheme-white; }

					& > div,
					& > div > b {
						color: $spec-bg-color--scheme-white; } } } } }

	&.schemecolor-blue {
		.file {
			&:hover {
				.file__info {
					border-color: $spec-text-color--scheme-blue;

					&::before {
						background-color: $spec-text-color--scheme-blue; }

					& > div,
					& > div > b {
						color: $spec-bg-color--scheme-blue; } } } } } }

.vspec {
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="search"],
	textarea {
		font-family: inherit;
		box-shadow: none;
		height: 50px;
		font-size: $font-size;
		line-height: 30px;
		color: inherit;
		padding: 8px 15px;
		border: 2px solid currentColor;
		transition: none;

		&.error {
			border-color: currentColor;
			box-shadow: none;
			color: inherit; }

		&:focus {
			box-shadow: none; }

		@include placeholder {
			color: currentColor; } } }

.btn-default, input[type="submit"], input[type="button"], input[type="reset"], button {
	.vspec & {
		font-family: inherit;
		color: $spec-text-color;
		border-color: $spec-text-color;
		background-color: $spec-bg-color;
		transition: none;

		&:hover {
			border-color: $spec-text-color;
			background-color: $spec-text-color;
			color: $spec-bg-color; } }

	.vspec.schemecolor-white & {
		color: $spec-text-color--scheme-white;
		border-color: $spec-text-color--scheme-white;
		background-color: $spec-bg-color--scheme-white;

		&:hover {
			color: $spec-bg-color--scheme-white;
			border-color: $spec-text-color--scheme-white;
			background-color: $spec-text-color--scheme-white; } }

	.vspec.schemecolor-blue & {
		color: $spec-text-color--scheme-blue;
		border-color: $spec-text-color--scheme-blue;
		background-color: $spec-bg-color--scheme-blue;

		&:hover {
			color: $spec-bg-color--scheme-blue;
			border-color: $spec-text-color--scheme-blue;
			background-color: $spec-text-color--scheme-blue; } } }

.vspec {
	.label-holder, label {
		color: inherit;
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit; } }

