/* slider reset */
.vspec {
	.uncos-slider,
	.questions-slider,
	.promo-slider,
	.case__list,
	.glance__slider {
		.slick-track {
			opacity: 1 !important;
			left: auto !important;
			width: auto !important;
			-webkit-transform: none !important;
			-ms-transform: none !important;
			transform: none !important;

			&::before, &::after {
				content: none; } }

		.slick-slide {
			position: relative !important;
			width: auto !important;
			height: auto !important;
			opacity: 1 !important;
			left: auto !important;
			top: auto !important;
			float: none !important;
			z-index: auto !important; }

		.slick-arrow {
			display: none !important; }

		.slick-dots {
			display: none !important; } } }

/* slider reset end */

@media (min-width: 980px) {
	.nav-opened .vspec .nav-overlay-page {
		display: none; } }

@media (min-width: 768px) {
	.nav-opened .vspec.main-page .nav-overlay-page {
		display: none; } }

@media (max-width: 479px) {
	.vspec .header .nav-overlay-page {
		height: calc(100% + 70px);
		top: -70px; } }

.vspec.inner-page .header {
	position: relative;
	left: auto;
	top: auto; }

.vspec {
	.header-holder {
		height: auto;
		border: 2px solid currentColor;
		border-width: 0 2px 2px 0;
		padding: 0; }

	&.main-page .header-holder {
		@include media('<tab-sm') {
			border-width: 0 0 2px 0; } }

	&.inner-page .header-holder {
		@include media('<tab-lg') {
			min-height: 142px;
			border-width: 0 0 2px 0; } } }

.vspec .header-options__holder {
	border-bottom: 2px solid currentColor; }

// logo
.vspec {
	.logo {
		box-shadow: none;
		border: 0 solid currentColor;
		border-width: 0 2px 2px 2px;
		box-sizing: border-box;
		height: 202px;

		//.schemecolor-white .logo > a::before,
		//.schemecolor-white .logo > span::before
		//webkit-filter: grayscale(1) contrast(3) invert(0)
		//ilter: grayscale(1) contrast(3) invert(0)

		//.schemecolor-blue .logo > a::before,
		//.schemecolor-blue .logo > span::before
		//webkit-filter: grayscale(1) contrast(1) invert(1)
		//ilter: grayscale(1) contrast(1) invert(1)

		@include media('<tab-sm') {
			height: 80px;
			border: none; }

		@include media('<359px') {
			width: 180px; }

		& > a, & > span {
			font-size: 7px; // нужно для свг-спрайта

			&::before {
				display: block;
				opacity: 1;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);

				width: 22.694375em;
				height: 18.430125em;
				background: {
					color: transparent;
					image: url(../img/logo.svg);
					size: 36.7361875em 150em;
					repeat: no-repeat;
					position: -1.25em -1.25em; }

				//+media('<tab-lg')
				//ackground-size: 130px auto

				//+media('<mob-lg')
 }				//ackground-position: $space-mob 50%

			&::after {
				display: none; } } }

	// for home page
	&.main-page .logo {
		height: 174px;

		@include media('<tab-sm') {
			height: 80px;

			& > a, & > span {
				font-size: 5px;

				&::before {
					background-size: 36.036188em 151.8em;
					background-position: -1.25em -105.9375em;
					width: 34.2361875em;
					height: 6.5186875em; } } } }

	// for inner page
	&.inner-page .logo {
		@include media('<tab-lg') {
			height: 80px;
			border: none;

			& > a, & > span {
				font-size: 5px;

				&::before {
					background-size: 36.036188em 151.8em;
					background-position: -1.25em -105.9375em;
					width: 34.2361875em;
					height: 6.5186875em; } } } }

	&.schemecolor-white {
		.logo {
			& > a, & > span {
				&::before {
					background-position: -1.25em -85em; } } }

		&.main-page .logo {
			@include media('<tab-sm') {
				& > a, & > span {
					&::before {
						background-position: -1.25em -141.7875em;
						width: 34.2361875em;
						height: 6.5186875em; } } } }

		&.inner-page .logo {
			@include media('<tab-lg') {
				& > a, & > span {
					&::before {
						background-position: -1.25em -141.7875em;
						width: 34.2361875em;
						height: 6.5186875em; } } } } }

	&.schemecolor-blue {
		.logo {
			& > a, & > span {
				&::before {
					background-position: -1.25em -22.1875em; } } }

		&.main-page .logo {
			@include media('<tab-sm') {
				& > a, & > span {
					&::before {
						background-position: -1.25em -115em;
						width: 34.2361875em;
						height: 6.5186875em; } } } }

		&.inner-page .logo {
			@include media('<tab-lg') {
				& > a, & > span {
					&::before {
						background-position: -1.25em -115em;
						width: 34.2361875em;
						height: 6.5186875em; } } } } } }

// questions-slider
.vspec .questions-slider {
	height: auto; }

.vspec .questions-slider .slick-track {
	column-count: 2;
	column-gap: 2em; }

.vspec.fontsize-small .questions-slider .slick-track {
	column-count: 3; }

.vspec.fontsize-large .questions-slider .slick-track {
	column-count: 1; }

@media (max-width: 979px) {
	.vspec .questions-slider .slick-track {
		column-count: 1;
		column-gap: 1.5em; }

	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 2; } }

@media (max-width: 767px) {
	.vspec.fontsize-small .questions-slider .slick-track {
		column-count: 1; } }

.vspec .questions-slider .questions__item {
	margin-bottom: $bottom-space;
	display: inline-block;
	width: 100% !important; }

.vspec .questions__item:last-child {
	margin-bottom: 0; }

.vspec .questions__item > a {
	display: inline;
	font-size: $item-font-size;
	line-height: $item-line-height;
	letter-spacing: inherit;
	padding: 0;
	min-height: 0;
	text-decoration: underline;
	-webkit-transition: none;
	transition: none; }

.vspec .questions__item > a:hover {
	text-decoration: none; }

.vspec .questions__item .questions__icon {
	display: none; }

.vspec .questions__item .questions__text {
	display: inline;
	letter-spacing: inherit;
	width: auto;
	padding: 0;
	margin-bottom: $bottom-space;
	text-align: left; }

.vspec .phones {
	box-sizing: border-box;
	box-shadow: none; }

.vspec .phs__list > .phs__item:before {
	border-color: #000;
