		padding: 0;
		margin: 0; }

	.tab-item-wrap {
		height: auto !important; }

	.tab-item {
		position: relative !important;
		width: auto !important;
		left: auto !important;
		top: auto !important;
		opacity: 1 !important;
		margin-bottom: $bottom-space-item; }

	.contacts-adr {
		background: none;
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		padding: 0; }

	.contacts-adr__title {
		display: block;
		font-size: $item-font-size;
		line-height: $item-line-height;
		letter-spacing: inherit;
		color: inherit;
		padding: 0;
		margin-bottom: $bottom-space;
		font-weight: normal; }

	.contacts-adr__item {
		overflow: visible;
		width: auto;
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		display: block;
		padding: 0;
		margin-bottom: $bottom-space;
		opacity: 1;
		transition: none;

		&::before {
			display: none; } }

	.contacts-adr__icon {
		display: none; }

	.contacts-adr__content {
		max-width: none;
		overflow: visible; }

	.contacts-adr__dd, .user-content .contacts-adr__dd {
		line-height: inherit;
		padding: 0;
		margin-bottom: $bottom-space;

		li {
			padding: 0;
			margin-bottom: round-float($bottom-space/2, 1); }

		.dt {
			float: none;
			width: auto;
			max-width: none; } }

	.contacts-adr__row_mark {
		color: inherit; }

	.contacts-map {
		display: none; }

	&.imageson .contacts-map {
		display: block;
		margin-bottom: $bottom-space-item; }

	.contacts-share-container {
		display: none; }

	.contacts-phones-container {
		background: none;
		padding: 0;
		margin-bottom: $bottom-space-section; }

	.contacts-phones {
		padding: 0;
		margin-bottom: $bottom-space-item;
		max-width: none; }

	.cph__row {
		display: block;
		display: flex;
		flex-direction: column;
		margin-bottom: $bottom-space-item; }

	.cph__dt {
		display: block;
		order: 2;
		width: auto;
		height: auto;
		font-size: inherit;
		line-height: inherit;
		font-weight: normal;
		letter-spacing: inherit;
		color: inherit;
		padding: 0; }

	.cph__dd {
		order: 1;
		padding: 0;
		font-size: $item-font-size;
		line-height: $item-line-height;
		font-weight: normal;
		margin-bottom: $bottom-space; }

	.cph__numbers {
		position: relative;
		visibility: visible;
		max-height: none;
		top: auto;
		left: auto;

		&::before {
			content: none; } }

	.cph__number {
		&, &:first-child ~ .cph__number {
			padding: 0;
			opacity: 1; }

		& > span > em {
			color: inherit;
			font-weight: 500; } }

	.cph__number_opener {
		&::after {
			content: none; }

		& > span {
			border: none;
			cursor: default; } }

	.callback-container {
		padding: 0;
		margin-bottom: $bottom-space-item;

		h2 {
			letter-spacing: inherit;
			font-weight: bold;
			text-transform: uppercase; } }

	.note {
		font-size: $font-size--small;
		line-height: $line-height--small;
		color: inherit;
		letter-spacing: inherit;
		padding: 0;
		margin-bottom: $bottom-space; }

	.parents-container {
		h2 {
			letter-spacing: inherit;
			font-weight: bold;
			text-transform: uppercase; } }

	.parents {
		border: none;
		display: block;
		width: auto; }

	.parents__item {
		border: none;
		display: block;
		text-align: left;
		margin-bottom: $bottom-space;

		& > a {
			display: inline;
			padding: 0;
			border: none;
			box-shadow: none;
			text-decoration: underline;

			&::before {
				content: none; }

			&:hover {
				text-decoration: none; } } }

	.parents__icon {
		position: relative;
		left: auto;
		top: auto;
		height: 80px;
		width: auto;
		text-align: left;
		display: none;
		margin: 0 0 $bottom-space; }

	&.imageson .parents__icon {
		display: block; }

	.parents__title {
		max-width: none;
		font-size: $item-font-size;
		line-height: $item-line-height;
		letter-spacing: normal;
		color: inherit;

		br {
			display: none; } } }

/* contacts end */

/* page 404 */
.vspec {
	&.page-404 {
		.wrapper {
			background: none; } }

	.error-404 {
		padding: 0;
		text-align: left;

		& > h1 {
			text-transform: uppercase; } }

	.error-404__note {
		font-size: inherit;
		line-height: inherit;
		color: inherit;
		letter-spacing: inherit;
		padding: 0;
		margin-bottom: $bottom-space; }

	.error-404__img {
		overflow: visible;
		max-width: none;
		text-indent: 0;
		font-size: $title-font-size;
		line-height: $title-line-height;
		text-transform: uppercase;
		text-align: inherit;
		background: none;
		margin: 0 0 $bottom-space;

		&::before {
			content: none; } }

	.error-404__list,
	.user-content .error-404__list {
		font-size: $item-font-size;
		line-height: $item-line-height;
		padding: 0;
		margin-bottom: $bottom-space;

		li {
			display: block;
			padding: 0;
			margin-bottom: round-float($bottom-space/2, 1);

			&::before {
				content: none; } }

		a {
			color: inherit; } } }

/* page 404 end */

/* layout spaces */
.vspec {
	.content,
	.window,
	.wit-holder,
	.promo-holder,
	.footer-contacts,
	.social-networks,
	.footer-holder,
	.site-map,
	.press-archive,
	.press-adt__item,
	.category-links,
	.questions,
	.main-contacts,
	.article-press {
		padding-left: $space;
		padding-right: $space;

		@include media('<mob-lg') {
			padding-left: $space-sm;
			padding-right: $space-sm; }

		@include media('<mob-md') {
			padding-left: $space-mob;
			padding-right: $space-mob; } }

	.class {
		margin-left: -$space;
		margin-right: -$space;

		@include media('<mob-lg') {
			margin-left: -$space-sm;
			margin-right: -$space-sm; }

		@include media('<mob-md') {
			margin-left: -$space-mob;
			margin-right: -$space-mob; } } }

/* layout spaces end */

